.cart-form {
  max-width: 496px;
  height: 420px;
  width: 100%;
  border: 2px solid #000;
  border-radius: 21px;
  padding: 25px;
  &__title {
    font-size: 28px;
    font-weight: 600;
    line-height: 36px;
  }

  &__row {
    margin-top: 47px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__total {
    font-size: 24px;
    font-weight: 500;
    line-height: 31px;
  }

  &__total-price {
    font-size: 32px;
    font-weight: 600;
    line-height: 42px;

    span {
      font-size: 10px;
      font-weight: 600;
      line-height: 13px;
    }
  }

  &__input {
    margin-top: 60px;
    max-width: 446px;
    width: 100%;
    font-size: 18px;
    font-weight: 500;
    line-height: 23px;
    padding: 22px 25px;
    border: 1px solid #000;
    border-radius: 20px;
  }

  &__btn {
    margin-top: 26px;
    padding: 20px;
    font-size: 28px;
    font-weight: 700;
    line-height: 36px;
    max-width: 446px;
    width: 100%;
    border-radius: 20px;
    color: #fff;
    background-color: #339933;
  }
}
